  background: #FF6431;
  font-size: 24px;
  font-weight: 500;
  line-height: 1;
}

.modal-content__body {
  padding: 20px;
  color: #6e6e6e;
}

.modal-content__body p {
  font-size: 16px;
  line-height: 24px;
}

.modal-content__body p:not(:last-child) {
  margin-bottom: 20px;
}

.modal-content__footer {
  padding: 20px;
  border-top: 1px solid #bdc3c7;
}

.modal-content__footer:after {
  content: " ";
  display: table;
  clear: both;
}

.modal-content__submit {
  float: right;
  min-width: 125px;
}

.modal-content__submit:not(:first-child) {
  margin-right: 20px;
}

.modal-item {
  position: relative;
}

.modal-item + .modal-item {
  margin-top: 20px;
}

.modal-item__label {
  position: absolute;
  top: 0;
  left: 0;
  width: 100px;
  line-height: 60px;
}

.modal-item__label span {
  display: inline-block;
  vertical-align: middle;
  font-size: 16px;
  line-height: 1.2;
}

.modal-item__label + .modal-item__row {
  padding-left: 100px;
}

.modal-item__row {
  margin: 0 -10px;
}

.modal-item__row:after {
  content: " ";
  display: table;
  clear: both;
}

.modal-item__col {
  float: left;
  padding: 0 10px;
  width: 50%;
}
